import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
} from '@coreui/react'
import { DocsLink } from 'src/reusable'
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import axios from 'axios'
import MaterialTable from 'material-table'



const Buttons = () => {
  const [spName, setspName] = useState('');
  const [sps, setsps] = useState([])
  const apiurl = 'https://jetek-tr5el.ondigitalocean.app/admin';
  const authRequest = axios.create({
    baseURL: apiurl,
    headers: {
      'auth-token': 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwNWZhNDliMjUzNjI1MjliMDMzMTBhMyIsImlhdCI6MTYxNzI3OTI5NSwiZXhwIjoxNjE3MzY1Njk1fQ.vUJVRdhU3sMKhnCrZIE9W-7fIAumUNQJ-N8vOIpP3XY'
    }
  })
  const StyledButton = withStyles({
    root: {
      background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
      borderRadius: 3,
      border: 0,
      color: 'white',
      height: 48,
      padding: '0 30px',
      boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      marginTop: 20
    },
    label: {
      textTransform: 'capitalize',
    },
  })(Button);
  const useStyles = makeStyles((theme) => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '25ch',

      },
    },
    paper: {
      position: 'absolute',
      width: 400,
      backgroundColor: theme.palette.background.paper,
      border: '2px solid #000',
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  }));
  const validateOrSubmin = () => {
    var body = {
      "name": spName
    }
    try {
      // const users = await authRequest.post(`/deleteUser`)

      axios.post('https://jetek-tr5el.ondigitalocean.app/admin/addspCatogare', body)
        .then(response => response)
        .then(data => console.log(data.status));
    } catch (error) {
      console.log(error);
    }
  }
  const columns = [
    { title: "الاسم ", field: "name" }
  ]

  const deleteSp = async (id) => {
    // console.log(id);
    var body = {
      "id": id
    }
    try {
      // const users = await authRequest.post(`/deleteUser`)

      axios.post('https://jetek-tr5el.ondigitalocean.app/admin/deleteSpCatogare', body)
        .then(response => response)
        .then(data => console.log(data));
    } catch (error) {
      console.log(error);
    }
  }

  const fetch = async () => {
    try {
      const captins = await authRequest.get(`/getAllSpCatogaries`)
      // handle success
      console.log(captins.data);
      await setsps(captins.data)
      // console.log("state seting");

    } catch (error) {
      console.log(error);
    }

  }
  useEffect(() => {

    fetch()
  }, [1000])
  return (
    <>
      <CCard>
        <CCardHeader>
          نقاط البيع
          <DocsLink name="-Button" />
        </CCardHeader>
        <CCardBody>
          <TextField
            required
            id="outlined-basic"
            label="الاسم "
            type=""
            variant="outlined"
            style={{ width: '100%' }}
            onChange={(event) => setspName(event.target.value)}
            helperText="متجر , مقهى"
          />
          <br />
          <StyledButton onClick={() => { validateOrSubmin() }} variant="contained" className="insert" color="primary" >
            إضافة
          </StyledButton>


          <MaterialTable
            title="بيانات المستخدمين"
            data={sps}
            columns={columns}
            options={{
              rowStyle: {
                backgroundColor: '#EEE',
              },
              exportButton: true,
            }}
            actions={[
              {
                icon: 'BuildIcon',
                tooltip: 'تعطيل الحساب ',

              },
              {
                icon: 'delete',
                tooltip: 'حذف المستخدم',
                onClick: (event, rowData) => deleteSp(rowData._id)
              }
            ]}
          />
        </CCardBody>
      </CCard>

    </>
  )
}

export default Buttons

